import React, { useEffect, useState } from 'react'
import { ShoeHavenApi as api } from '../ShoeHavenApi'
import { Card, Button } from 'react-bootstrap'

const Purchase = ({ listing, purchase, returnItem }) => {
    const [item, setItem] = useState({})

    useEffect(() => {
        async function getPurchase() {
            let res = await api.request(`listings/${listing}`)
            setItem(res)
        }
        getPurchase()
    }, [listing])
    

    return (
        <Card>
            <Card.Body>
                <Card.Title>{item.title} / ${item.price}</Card.Title>
                <Card.Img variant="top" src={item.picture} alt='Cool Listing' />
            </Card.Body>
            <Button onClick={async () => await returnItem(purchase)}>
                Return This Item
            </Button>
        </Card>
    )
}

export default Purchase