import React, { useEffect } from 'react'
import {Button} from 'react-bootstrap'
import {Link, useParams} from 'react-router-dom'
import {ShoeHavenApi as api} from '../ShoeHavenApi'

const OrderPlaced = () => {
    let {session_id} = useParams()    

    useEffect(() => {
        async function recordSaleInternally(){
            await api.request(`sales/new`,{session_id}, 'post')
        }
        recordSaleInternally()
    },[session_id])
    
    return(
        <div>
            <h1>Order Placed</h1>
            <Button as={Link} to='/listings'>Return To Listings</Button>
        </div>
    )
}

export default OrderPlaced